.preview-services {
    background-image: url('/img/placeholder/beverages-brunch-cocktail-5317.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    min-height: calc(100vh - 167.5px);
    h3 {
        text-align: center;
        font-size: 4rem;
        color: #fff;
        text-shadow: 1px 1px 2px black; }
    &__card {
        height: 50vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        h4 {
            font-size: 2rem; }
        p {
            text-align: center; }
        a {
            border-color: #000 !important;
            transition: 0.5s;
            padding: 0.35rem;
            padding-top: 0.55rem;
            &:hover {
                text-decoration: none;
                transform: scale(1.05);
                color: black; } }
        .rounded-circle {
            width: 100px;
            margin: auto;
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #e4822e;
            border: 2px solid;
            transition: 1.5s;
            &:hover {
                transform: rotateY(180deg); }
            svg {
                font-size: 2.5rem; } } } }
